<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="6">
        <el-form-item>
          <h2>ようこそ！</h2>
          <h5>アカウント又はメールでサインインしてください。</h5>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="6">
        <el-form-item label="" prop="name">
          <el-input type="text" v-model="ruleForm.name" autocomplete="off"
                    placeholder="アカウント/メール"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="6">
        <el-form-item label="" prop="pass">
          <el-input type="password" v-model="ruleForm.pass" autocomplete="off" placeholder="パスワード"></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="6">
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">サインイン</el-button>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="6">
        <el-form-item>
          <el-link href="/#/resetpassword" :underline="false" type="primary">アカウント/パスワードを忘れですか?</el-link>
          <el-divider direction="vertical"></el-divider>
          <el-link href="/#/signup" :underline="false" type="primary">登録</el-link>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
import {Signin} from 'api/user'

export default {
  name: 'SignIn',
  data() {
    let validateUser = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('User ID is required.'))
      } else {
        // if (this.ruleForm.login_user !== '') {
        //   this.$refs.ruleForm.validateField('login_user')
        // }
        callback()
      }
    }
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('Password id required.'))
        // } else if (value !== this.ruleForm.pass) {
        //   callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        name: '',
        pass: ''
      },
      rules: {
        name: [
          {validator: validateUser, trigger: 'blur'}
        ],
        pass: [
          {validator: validatePass, trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    Signin() {
      let values = {}
      values['username'] = this.ruleForm.name
      values['password'] = this.ruleForm.pass
      Signin(values).then(res => {
        console.log(res)
        if (res.data.status == 1) {
          console.log(res.status);
          this.$router.push('ProjectList')
        }
      }).catch(err => {
        console.log(err)
      })
    },
    submitForm(ruleForm) {
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          alert('このフォームを送信しますか？')
          this.Signin()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style scoped>
.el-button {
  width: 100%;
}
</style>
